import React from 'react'
import { NavLink } from 'react-router-dom'
import '../Comman.css'
import { useDispatch, useSelector } from "react-redux";

import axios from "axios";
import { authActions } from '../../store';
axios.defaults.withCredentials = true

const Header = () => {

    const dispatch = useDispatch();

    const isLoggedIn = useSelector(state => state.isLoggedIn);
    const sendLogoutReq = async () => {
        const res = await axios.post("https://icloudsoft.onrender.com/api/logout", null, {
            withCredentials: true
        });
        if (res.status === 200) {
            return res
        }
        return new Error("Unable to Logoute. Please try again")
    }

    const handleLogout = () => {
        sendLogoutReq().then(() => dispatch(authActions.logout()));
    }

    return (
        <>
            <nav className="navbar navbar-expand-lg p-0 text-navy header-content ">

                <div className="container-fluid p-3 ">
                    <div><NavLink to="/"><img className='p-1' style={{ height: "4rem" }} src={require("../../Image/icloudsoft-logo.png")} alt="icloudsoft_logo" /></NavLink></div>
                    <button className="navbar-toggler" type="button " data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                        <i className="fa-solid fa-bars"></i>
                    </button>
                    <div className="collapse navbar-collapse mt-lg-0  mt-3 " id="navbarNavDropdown">
                        <ul className="navbar-nav    d-flex justify-content-lg-around align-items-lg-center w-100 ">

                            <li className='nav-item'>
                                <NavLink className="text-dark  text-decoration-none fs-6" to="/" > <b className='text-navy '>Home</b> </NavLink>
                            </li>
                            <li className='nav-item' >
                                <NavLink className="text-dark text-decoration-none fs-6" to="/company"> <b className='text-navy'>Company</b> </NavLink>
                            </li>

                            <li className="nav-item #submenu nav-link" data-bs-toggle="collapse" aria-current="true" >
                                <div className="dropdown-toggle text-navy fs-6" id="mega-one" role="button" data-bs-toggle="dropdown" aria-expanded="true"><b>Solutions</b></div>
                                <ul className="dropdown-menu nav collapse p-2 bg-light  w-100 " aria-labelledby="mega-one" style={{ zIndex: "6" }} id="submenu">
                                    <li className='nav-itemn   text-white'>
                                        <div className='container '>
                                            <div className="row d-flex justify-content-around fs-6">
                                                <div className="col-lg-2 col-md-4 col-sm-6 col-12 mb-3 ">
                                                    <NavLink className='text-decoration-none' to="/solutions/googlecloudplatform">
                                                        <div className='dropdown-item text-navy fs-5'><b>GCP</b></div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/googlecloudplatform" >
                                                        <div className='dropdown-item'>Google Cloud Platform</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/googleworkspace" >
                                                        <div className='dropdown-item'>Google Workspace</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                </div>
                                                <div className="col-lg-2 col-md-4 col-sm-6 col-12 mb-3 ">
                                                    <NavLink className='text-decoration-none' to="/solutions/amazonwebservices" >
                                                        <div className='dropdown-item text-navy fs-5'><b>AWS</b> </div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/amazonwebservices" >
                                                        <div className='dropdown-item'>Amazon Web Services</div>
                                                        <div className="border" ></div>
                                                    </NavLink>

                                                </div>
                                                <div className="col-lg-2 col-md-4 col-sm-6 col-12 mb-3">
                                                    <NavLink className='text-decoration-none' to="/solutions/microsoft365" >
                                                        <div className='dropdown-item text-navy fs-5'><b>Microsoft</b></div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/microsoft365" >
                                                        <div className='dropdown-item'>Microsoft 365</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/microsoftdynamic365" >
                                                        <div className='dropdown-item'>Microsoft Dynamics 365</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/microsoftazure" >
                                                        <div className='dropdown-item'>Microsoft Azure</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                </div>
                                                <div className="col-lg-2 col-md-4 col-sm-6 col-12 mb-3 ">
                                                    <NavLink className='text-decoration-none' to="/solutions/cloudmange" >
                                                        <div className='dropdown-item text-navy fs-5'><b>Hybrid Cloud</b></div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/cloudmange" >
                                                        <div className='dropdown-item'>Cloud Manage</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/cloudMigration" >
                                                        <div className='dropdown-item'>Cloud Migration</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/privateCloud" >
                                                        <div className='dropdown-item'>Private Cloud</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/devops" >
                                                        <div className='dropdown-item'>DevOps</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/iot" >
                                                        <div className='dropdown-item'>IOT</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/cloudDisasterRecovery" >
                                                        <div className='dropdown-item'>Cloud Disaster</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                </div>
                                                <div className="col-lg-2 col-md-4 col-sm-6 col-12 mb-3">
                                                    <NavLink className='text-decoration-none' to="/solutions/acronis">
                                                        <div className='dropdown-item text-navy fs-5'><b>Backup</b></div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/acronis">
                                                        <div className='dropdown-item'>Acronis</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                    <NavLink className='text-decoration-none' to="/solutions/veeam" >
                                                        <div className='dropdown-item'>Veeam</div>
                                                        <div className="border" ></div>
                                                    </NavLink>
                                                </div>
                                            </div>
                                        </div>
                                    </li>
                                </ul>
                            </li>


                            <li className='nav-item'>
                                <NavLink className="text-decoration-none fs-6" to="/blogs"  > <b className='text-navy'>Blogs</b></NavLink>
                            </li>
                            <li className='nav-item'>
                                <NavLink className="text-decoration-none fs-6" to="/contact"  ><b className="text-navy">Contact</b> </NavLink>
                            </li>
                            <li className='nav-item'>
                                <NavLink className="text-decoration-none fs-6 text-navy " to="tel:+91 95274688989"><b> +91 9527468898 </b></NavLink>

                            </li>

                            


                            <div className='row d-flex justify-content-around align-items-center'>
                                <div className='col-lg-2 col-md-3 col-2  d-flex justify-content-center'>
                                    <button className=" btn  bg-body" style={{ borderRadius: "10px 10px 0px 10px" }}>
                                        <i className="fa-solid fa-magnifying-glass"></i>
                                    </button>
                                </div>

                                {/* <div className=' col-lg-2 col-md-3 col-2 d-flex justify-content-center'>
                                    <button className='btn bg-light'><i className="fa-solid fa-globe p-2"></i></button>
                                </div> */}


                                <div className=' col-lg-3 col-md-3 col-3 d-flex justify-content-center'>
                                    <button className=' btn d-flex align-items-center'>


                                        {!isLoggedIn && <>
                                            <li className="nav-item active ">
                                                <NavLink to="/login" className="fs-6 nav-link text-dark border border-bottom-5 border-danger border-top-0 border-start-0 border-end-0" href="#"><b>LOGIN</b></NavLink>
                                            </li>

                                            <li className="nav-item active p-3">
                                                <NavLink  to="/signup" className="fs-6 nav-link text-dark border border-bottom-5 border-primary border-top-0 border-start-0 border-end-0 " href="#"><b>SIGNUP</b></NavLink>
                                            </li>
                                        </>
                                        }

                                        {
                                            isLoggedIn && (
                                                <li className="nav-item">
                                                    <NavLink onClick={handleLogout} to="/" className="nav-link text-dark " href="#">LOGOUT</NavLink>
                                                </li>
                                            )
                                        }{" "}
                                        {/* <NavLink className="text-decoration-none fs-6 text-navy fw-bold" to="/signup"><span> Login </span></NavLink> */}
                                    </button>
                                </div>
                                <div className='col-lg-5 col-md-3 col-5 d-flex justify-content-center  '>
                                    <button className='btn bg-success text-white  text-center w-100'>Try Free demo</button>
                                </div>



                            </div>
                        </ul>
                    </div>
                </div>
            </nav>

        </>
    )
}

export default Header