import React from 'react'
import { Routes, Route } from 'react-router'
import Home from './Home'
import Header from './comman/Header'
import Contact from './Contact'
import Company from './Company'
import Footer from './comman/Footer'
import Googlecloudplatform from './solutions/Googlecloudplatform'
import Googleworkspace from './solutions/Googleworkspace'
import Amazonwebservices from './solutions/Amazonwebservices'
import Microsoft365 from './solutions/Microsoft365'
import Microsoftdynamic365 from './solutions/Microsoftdynamic365'
import Microsoftazure from './solutions/Microsoftazure'
import Cloudmange from './solutions/Cloudmange'
import Cloudmigration from './solutions/Cloudmigration'
import Privatecloud from './solutions/Privatecloud'
import DevOps from './solutions/DevOps'
import IOT from './solutions/IOT'
import CloudDisaster from './solutions/CloudDisaster'
import Acronis from './solutions/Acronis'
import Veeam from './solutions/Veeam'
import Blogs from './Blogs'
import Eorror from './Eorror'
import Signup from './comman/Signup'
import Login from './comman/Login'
import Welcome from './comman/Welcome'
import { useSelector } from "react-redux";

const Routing = () => {

    const isLoggedIn = useSelector(state=> state.isLoggedIn);
    console.log(isLoggedIn);

    return (
        <>
            <React.Fragment>
                <header className='sticky-top bg-light'>
                    <Header />
                </header>
                <main>
                    <Routes>
                        <Route to path="/" element={<Home />} />
                        <Route to path="/contact" element={<Contact />} />
                        <Route to path="/company" element={<Company />} />
                        <Route to path="/blogs" element={<Blogs />} />
                        <Route to path='/solutions/googlecloudplatform' element={<Googlecloudplatform />} />
                        <Route to path='/solutions/googleworkspace' element={<Googleworkspace />} />
                        <Route to path='/solutions/amazonwebservices' element={<Amazonwebservices />} />
                        <Route to path='/solutions/microsoft365' element={<Microsoft365 />} />
                        <Route to path='/solutions/microsoftdynamic365' element={<Microsoftdynamic365 />} />
                        <Route to path='/solutions/microsoftazure' element={<Microsoftazure />} />
                        <Route to path='/solutions/cloudmange' element={<Cloudmange />} />
                        <Route to path='/solutions/cloudmigration' element={<Cloudmigration />} />
                        <Route to path='/solutions/privateCloud' element={<Privatecloud />} />
                        <Route to path='/solutions/devops' element={<DevOps />} />
                        <Route to path='/solutions/iot' element={<IOT />} />
                        <Route to path='/solutions/cloudDisasterRecovery' element={<CloudDisaster />} />
                        <Route to path='/solutions/acronis' element={<Acronis />} />
                        <Route to path='/solutions/veeam' element={<Veeam />} />
                        <Route to path ="*" element={<Eorror/>} />
                        <Route to path ="/signup" element={<Signup/>} />
                        <Route to path ="/login" element={<Login/>} />
                        <Route to path ="/user" element={<Welcome/>} />
                        {isLoggedIn &&
                            <Route path="/user" element={<Welcome />} />
                        } {" "}
                    </Routes>
                </main>
                <footer>

                    <Footer />
                </footer>
            </React.Fragment>

        </>
    )
}

export default Routing