import React from 'react'

const Blogs = () => {
    return (
        <div>
            <video autoPlay loop muted className='w-100 ' style={{height:"450px",  objectFit: "fill" }}  >
                <source src={require("../Images/IcloudSoftCompany.mp4")} type="video/mp4" />
                {/* <source src="movie.ogg" type="video/ogg"/> */}
                Your browser does not support the video tag.
            </video>
        </div>
    )
}

export default Blogs