// import { MiddlewareArray } from "@reduxjs/toolkit";
import axios from "axios";
import React, { useEffect, useState } from "react";
axios.defaults.withCredentials = true;
let firstRender = true;


const Welcome = () => {

    const [user, setUser] = useState([]);
    console.log(user)

    const refreshToken = async () => {
        const res = await axios
            .get('http://localhost:5050/api/refresh', { withCredentials: true })
            .catch(err => console.log(err));

        const data = await res.data;
        return data;
    }

    const sendReq = async () => {
        const res = await axios
            .get('http://localhost:5050/api/user', { withCredentials: true })
            .catch(err => console.log(err));
        const data = await res.data;
        console.log(data);
        return data
    }


    // useEffect(() => {
    //     if (firstRender) {
    //         // firstRender = false
    //         // sendReq().then((data) => setUser(data.user));
    //         // sendReq().then((data)=>setUser(user))

    //     }
    //     // else{
    //     //     refreshToken().then(data=>setUser(data.user));
    //     // }
    // }, []
    // )




    useEffect(() => {
        if (firstRender) {
            firstRender = false
            // sendReq().then((data)=> setUser(data.user));
            sendReq().then(
                (data) => {
                    console.log(data)

                    setUser(data.user)
                })



        }
        let interval = setInterval(() => {
            refreshToken().then(data => setUser(data.user));
        }, 1000 * 29)

        return () => clearInterval(interval)

    }, []);

    return (
        <div className="p-3 text-center mt- mb-3">
            <h3 className="p-3 fs-2 mb-3">Welcome To iCloudSoft Technology</h3>
            {user.name && <h4 className="fw-bold fs-3 "><i>
                {user.name}
            </i>
            </h4>}
        </div>
    )
}

export default Welcome;