import React from 'react'
import { Helmet, HelmetProvider } from 'react-helmet-async'
const Microsoftazure = () => {
    return (
        <>


            <HelmetProvider>
                <Helmet>

                    <meta charset="utf-8" />
                    <link rel="canonical" href="https://www.icloudsoft.in/microsoftazure"  />
                    <meta name="description" content="Get the most out of your cloud solutions with the help of our partners! We provide the best cloud services in Pune." />
                    <meta name="keywords" content="Cloud Solution Company In Pune, iCloudsoft Technology ,Microsoft, Azure" />
                    <title>  Microsoft Partner | Cloud Solutions Provider in Pune </title>
                </Helmet>
            </HelmetProvider>

            <section>
                <div className='container-fluid d-none d-lg-block  ' style={{ backgroundImage: `url(${require("../../Image/Azure.png")})`, backgroundAttachment: "local", backgroundRepeat: "no-repeat", backgroundSize: "cover", backgroundPosition: '100%' }}>
                    <div className='container' >
                        <div className='row d-flex text-white align-items-center' style={{ height: "400px " }}>

                            <div className='p-4 '>
                                <h1>Microsoft Azure</h1>
                                <h5>Bulid with iCloudsoft on Azure</h5>


                            </div>

                        </div>
                    </div>
                </div>


                <div className='container-fluid d-lg-none w-100 ' style={{ backgroundImage: `url(${require("../../Image/Azure.png")})`, backgroundAttachment: "local", backgroundRepeat: "no-repeat", backgroundSize: "cover", backgroundPosition: 'right' }}>
                    <div className='container' style={{ background: "rgba(0,0,0,0.5)" }}>
                        <div className='row d-flex text-white align-items-center' style={{ height: "400px " }}>

                            <div className=' '>
                                <h2 className='fs-1'>Microsoft Azure</h2>
                                <h5>Bulid with iCloudsoft on Azure</h5>


                            </div>

                        </div>
                    </div>
                </div>
            </section>

            <section style={{ backgroundColor: "#FFFFFF" }}>
                <div className="container p-4  mt-3">
                    <h2 className='fs-1 text-center'><b>iCloudsoft Enabling Business To Run On Azure</b></h2>

                    <p className='fs-5'>Looking for hassle-free migration of your on-premise application to Microsoft Cloud? Look no further than iCLoudSoft, a trusted silver partner of Microsoft. Our expert team offers affordable and reliable migration solutions to clients across India. Find proven and easy-to-implement solutions to your migration problems, backed by round-the-clock support services.</p>
                </div>

            </section>

            <section style={{ backgroundColor: "#F5F5F5" }}>
                <div className="container text-center  p-2">
                    <h3 className='fs-2'><b>Azure Products And Services</b></h3>

                </div>
            </section>

            <section style={{ backgroundColor: "#F5F5F5" }}>
                <div className="container ">
                    <div className="row text-center p-3 g-4">
                        <div className="col-lg-3 text-white  col-sm-6" style={{ backgroundColor: "#002e4a", borderRadius: "20px", border: "5px solid white" }}>
                            <div className=''>
                                <img src={require("../../Image/azure-compute.png")} alt="azure-compute" />

                                <h4 className='p-3 border border-bottom-3 border-warning border-top-0 border-start-0 border-end-0'><b>Compute Services</b></h4>
                                <p className='p-2'>Azure provides a virtual machine service that allows users to launch general-purpose virtual machines as part of its compute services.</p>
                            </div>
                        </div>

                        <div className="col-lg-3 text-white col-sm-6" style={{ borderRadius: "20px", backgroundColor: "#002e4a", border: "5px solid white" }}>
                            <div className=''>
                                <img src={require("../../Image/azure-mobile.png")} alt="azure-mobile" />

                                <h4 className=' p-3 border border-bottom-3 border-warning border-top-0 border-start-0 border-end-0'><b>Mobile services</b></h4>
                                <p className='p-2'>Mobile engagement in Azure allows you to collect real-time analytics on users' behavior and send push notifications to engage with them.</p>
                            </div>
                        </div>

                        <div className="col-lg-3 text-white col-sm-6" style={{ borderRadius: "20px", backgroundColor: "#002e4a", border: "5px solid white" }}>
                            <div className=''>
                                <img src={require("../../Image/azure-storage.png")} alt="azure-storage" />

                                <h4 className=' p-3 border border-bottom-3 border-warning border-top-0 border-start-0 border-end-0'><b>Storage services</b></h4>
                                <p className='p-2'>Azure's storage services offer REST and SDK APIs that enable users to securely store and access their data on the cloud.</p>
                            </div>
                        </div>

                        <div className="col-lg-3 text-white col-sm-6" style={{ borderRadius: "20px ", backgroundColor: "#002e4a", border: "5px solid white" }} >
                            <div className=''>
                                <img src={require("../../Image/azure data management.png")} alt="azure data management" />

                                <h4 className=' p-3 border border-bottom-3 border-warning border-top-0 border-start-0 border-end-0'><b>Data Management</b></h4>
                                <p className='p-2'>Azure Search is a cloud-based search service that enables developers to add search capabilities to their applications.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </>
    )
}

export default Microsoftazure