import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { authActions } from "../../store";



const Login = () => {

    const dispatch = useDispatch();

    const history = useNavigate();

    const [inputs, setInputes] = useState({
        email: "",
        password: ""
    });

    const handleChange = (e) => {
        setInputes(prev => ({
            ...prev,
            [e.target.name]: e.target.value
        }))
    };

    const sendRequest = async () => {
        const res = await axios.post('https://icloudsoft.onrender.com/api/login', {
            email: inputs.email,
            password: inputs.password

            
        }).catch((err) => console.log(err));

        const data = await res.data;
        console.log(data,"24")
        return data;

       
    }

  
      const handleSubmit = (e) => {
        e.preventDefault();
       
        sendRequest().then(() =>dispatch(authActions.login())).then(() => history("/user"));
    };


    return (

        <div className="container mt-3 mb-4">
            <div className="row  d-flex justify-content-center align-items-center">
                <div className="text-white p-5" style={{ width: "500px" ,backgroundColor:"#002E4A"}}>

                    <form onSubmit={handleSubmit}>
                        <h2 className="p-4 mb-4 text-center text-success" style={{borderBottom:"2px solid white"}}><i><b>LOGIN FORM</b></i></h2>

                        <div className="form-outline mb-4">
                            <label className="form-label" for="form2Example1"><i>Email address</i></label>

                            <input type="email" id="form2Example1" className="form-control"
                             name = "email"
                             onChange={handleChange}
                             value = {inputs.email} />
                        </div>

                        <div className="form-outline mb-4">
                            <label className="form-label fw-bold" for="form2Example2"><i>Password</i></label>

                            <input type="password" id="form2Example2" className="form-control" 
                             name = "password"
                             onChange={handleChange}
                             value = {inputs.password}/>
                        </div>

                        <div className="p-3 d-flex  justify-content-center align-items-center">
                            <button type="submit" className="btn w-50 fw-bold btn-outline-light" >LOGIN</button>
                        </div>

                    </form>
                </div>
            </div>
        </div>
    )
}

export default Login;