import axios from 'axios';
import React, { useState } from 'react'
import { NavLink, useNavigate } from 'react-router-dom';

export const Signup = () => {

    const history = useNavigate();

    const [inputs, setInputes] = useState({
        name: "",
        email: "",
        password: ""
    });

    const handleChange = (e) => {
        setInputes(prev => ({
            ...prev,
            [e.target.name]: e.target.value
        }))
       
    };

    const sendRequest = async () => {
                const res = await axios.post('https://icloudsoft.onrender.com/api/signup', {
                    name: inputs.name,
                    email: inputs.email,
                    password: inputs.password
                })
              .catch((err) => console.log(err));
                 const data = await res.data;
                 return data;
         };

    const handleSubmit = (e) => {
        e.preventDefault();
        
        sendRequest().then(()=>history("/login"));
    };

    return (
        <>
            <div className="container  mb-3" >
                <div className="row p-3 d-flex justify-content-center align-items-center">
                    <div className="text-white p-4" style={{ width: "530px", backgroundColor: "#002E4A" }}>
                       

                        <form onSubmit={handleSubmit}>
                            <h2 className="p-3 text-center text-success" style={{ borderBottom: "2px solid white" }}><i><b>SIGNUP FORM</b></i></h2>
                            <div className="form-outline mb-4">
                                <label className="form-label fw-bold" htmlFor="form2Example1"><i>Name</i></label>
                                <input type="text" id="form2Example1" className="form-control"
                                    name="name"
                                    onChange={handleChange}
                                    value={inputs.name} />
                            </div>

                            <div className="form-outline mb-4">
                                <label className="form-label fw-bold" htmlFor="form2Example1"><i>Email address</i></label>

                                <input type="email" id="form2Example1" className="form-control"
                                    name="email"
                                    onChange={handleChange}
                                    value={inputs.email} />
                            </div>

                            <div className="form-outline mb-4">
                                <label className="form-label fw-bold" htmlFor="form2Example2"><i>Password</i></label>

                                <input type="password" id="form2Example2" className="form-control"
                                    name="password"
                                    onChange={handleChange}
                                    value={inputs.password} />
                            </div>

                            <div className="d-flex  justify-content-center align-items-center">
                                <button type="submit" className="btn  w-50 fw-bold btn-outline-light">SIGNUP</button>
                            </div>

                            <div className='text-center '>
                                <h5 className='p-3 fs-6'><i>if allready logged in then login</i></h5>
                                <NavLink to="/login"><button type="submit" className="btn  w-50 fw-bold btn-outline-light">LOGIN</button></NavLink>
                            </div>

                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Signup;
