import React, { useRef, useState } from 'react'
import { NavLink } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import axios from 'axios';

const Contact = () => {

    const form = useRef();

    const [fullName, setFullName] = useState("");

    const [number, setNumber] = useState("");

    const [email, setEmail] = useState("");

    const [massage, setMassage] = useState("");

    const [errors, setErrors] = useState({});

    const loginHandle = (e) => {
        e.preventDefault();

        const errors = validateForm();

        if (Object.keys(errors).length === 0) {
            errors.fullName = ""; errors.number = ""; errors.email = ""; errors.massage = "";


            // const sendRequest = async () => {
            const user = {
                fullName: fullName,
                email: email,
                number: number,
                massage: massage
            }
           
           

            console.log(user);

            axios.post('https://icloudsoft.onrender.com/api/contactus',user)
                .then((res) => {
                    // console.log(res.data.massage)
                    const result = res.data.massage
                    alert(result)
                    setFullName(""); setEmail(""); setNumber(""); setMassage("");
                    setErrors({ fullName: "", email: "", number: "", massage: "" })
        
                    return res;
                })
                .catch(err => {
                    
                    console.log(err);
                    alert("unsuccess",err)
                    return err;
                })
           

        }


        // }

        // var url = "https://wa.me/9527468898?text="
        //     + "NAME:" + fullName + "NUMBER:" + number + "EMAIL:" + email + "MASSAGE:" + massage;

        // window.open(url, '_blank').focus();


    
        else {
    setErrors(errors);
}

    }

const validateForm = () => {
    const errors = {};
    // console.log(errors)
    var mailformat = /\S+@\S+\.\S+/;

    if (!fullName) {
        errors.fullName = "Names Is Requried";
    }
    else if (fullName.length < 4) {
        errors.fullName = "Names Requried At Least Four Characters"
    }

    if (!number) {
        errors.number = "Number Is Requried";
    }
    else if (number.length !== 10) {
        errors.number = "Number Must Have 10 Digit";
    }

    if (!email) {
        errors.email = "Email Is Requried";
    }
    if (email) {
        if (email.match(mailformat)) {

        }
        else {
            errors.email = `Email Must Be In " Text + @ + Text + . + Text " Format`
        }
    }

    if (!massage) {
        errors.massage = "Massage Is Requried";
    }
    return errors;
}

return (
    <>

        <Helmet>

            <meta charset="utf-8" />
            <link rel="canonical" href="https://www.icloudsoft.in/contact" />
            <meta name="description" content="Get the most out of your cloud solutions with the help of our partners! We have provide the best cloud services in Pune." />
            <meta name="keywords" content="We are here to help HEAD OFFICE, PUNE" />
            <title>Contact Us | iCloudsoft Technology | Cloud Solutions Provider in Pune </title>
        </Helmet>



        <div>
            <section>
                <div className='container-fluid d-none d-lg-block  ' style={{ backgroundImage: `url(${require("../Image/ContactUsBanner.jpg")})`, backgroundAttachment: "local", backgroundRepeat: "no-repeat", backgroundSize: "cover", backgroundPosition: '100%' }}>
                    <div className='container' >
                        <div className='row d-flex text-white align-items-center' style={{ height: "400px " }}>

                            <div className='p-4'>
                                <h1>How can we help you?</h1>
                            </div>

                        </div>
                    </div>
                </div>


                <div className='container-fluid p-0 d-lg-none w-100 ' style={{ backgroundImage: `url(${require("../Image/ContactUsBanner.jpg")})`, backgroundAttachment: "local", backgroundRepeat: "no-repeat", backgroundSize: "cover", backgroundPosition: 'right' }}>
                    <div className='container-fluid' style={{ background: "rgba(0,0,0,0.5)" }}>
                        <div className='row d-flex text-white align-items-center' style={{ height: "400px " }}>

                            <div className='p-4'>
                                <h2 className="fs-1">How can we help you?</h2>
                            </div>

                        </div>
                    </div>
                </div>
            </section>


            <div className='container-fluid mt-4 mb-4' >

                <div className='container text-white p-3 shadow' style={{ backgroundColor: "#002E4A" }}>

                    <div className='row pt-4 pb-3 d-flex justify-content-center align-items-center ' data-aos="zoom-in" >
                        <div className="col-lg-6 ">
                            <div className='container '  >
                                <h5 className='text-center fw-bold'><i>GET IN TOUCH</i></h5>
                                <form ref={form} onSubmit={loginHandle} className="row g-5 p-4  shadow-lg" style={{ backgroundColor: "#002E4A", border: "1px solid skyblue" }}>

                                    <div class="col-lg-12 ">
                                        <input type="text" className="form-control boderRadius border border-dark text-dark " id="inputText1" placeholder=" Full Name"
                                            onChange={e => setFullName(e.target.value)}
                                            value={fullName} />
                                        {errors.fullName && <div className='text-danger'>{errors.fullName}</div>}
                                    </div>

                                    <div className='col-lg-12'>
                                        <div className='row g-5'>
                                            <div className="col-lg-6">
                                                <input type="number" className="form-control boderRadius border border-dark text-dark" id="inputNumber" placeholder="Number" name="number" onChange={e => setNumber(e.target.value)} value={number} />
                                                {errors.number && <div className='text-danger'>{errors.number}</div>}
                                            </div>

                                            <div className="col-lg-6">
                                                <input type="email" className="form-control boderRadius border border-dark text-dark" id="inputEmail4" placeholder="Email Address" name="email" onChange={e => setEmail(e.target.value)} value={email} />
                                                {errors.email && <div className='text-danger'>{errors.email}</div>}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <textarea type="text" className="form-control boderRadius border border-dark text-dark " id="inputText3" placeholder="Message" name="message" onChange={e => setMassage(e.target.value)} value={massage} />
                                        {errors.massage && <div className='text-danger'>{errors.massage}</div>}
                                    </div>

                                    <div className="col-lg-12">
                                        <button type="submit" className="btn btn-light btn-outline-dark" >Send Message</button>
                                    </div>
                                </form>
                            </div>
                        </div>

                        <div className="col-lg-6 p-3">
                            <div className='ms-lg-4'>
                                <h2 className='text-uppercase  fw-bold  text-lg-center  fs-4'><i className='fas fa-home me-3'></i><i>HEAD OFFICE, PUNE</i></h2>
                                <hr />
                                <h4><i>iCloudsoft Technology Office No 601, 6th Floor, Lotus Building, Opp Joshi Railway Museum, Kothrud, Pune 411038</i></h4>
                            </div>

                            {/* <div className='ms-lg-4'>
                                    <h3 className='text-uppercase mt-5 fw-bold text-lg-center fs-5'><i className='fas fa-home me-3'></i>DHARASHIV</h3>
                                    <hr />
                                    <h5>iCloudsoft Technology Near Z.P. School, Khandeshwarswadi,Paranda Dharashiv-413505</h5>
                                </div> */}
                        </div>
                    </div>
                </div>
            </div>


            <div className=' mt-4 mb-2' >
                <div className='container text-center '>
                    <div className='row '>
                        <div className="col-lg-4">
                            <div className='text-center'>
                                <i className="fa-solid fa-phone text-center fs-4 mb-3"></i>

                            </div>

                            <h4 className='fs-4 text-center '>Call Us</h4>
                            <NavLink className='text-decoration-none text-dark ' to='tel:9527468898, 8766779267'> <h6 className='mt-3 fs-5 ps-4 pe-4'>+91 9527468898, +91 8766779267</h6></NavLink>
                        </div>

                        <div className="col-lg-4 ">
                            <div className='text-center'>
                                <NavLink className='text-decoration-none text-dark ' target='_blank' to='https://www.google.com/maps/dir//Offices+No+601,+6th+Floor,+iCloudsoft+Technology,+Lotus+,+Opp+Joshi+Railway+Museum,+Kothrud,+Pune,+Maharashtra+411038/@18.5013998,73.781925,14z/data=!4m8!4m7!1m0!1m5!1m1!1s0x3bc2bf285b81dd75:0x344ffe59f81b4da1!2m2!1d73.8200338!2d18.5013998'><i className="fa-solid fa-location-dot fs-2 mb-2"></i> </NavLink>

                            </div>

                            <h4 className=' fs-4 '>Address</h4>
                            <h6 className='fs-5  text-center'>iCloudsoft Technology Office No 601, 6th Floor, Lotus Building, Opp Joshi Railway Museum, Kothrud, Pune 411038</h6>
                        </div>
                        <div className="col-lg-4">
                            <div className='text-center'>
                                <i className="fa-solid fa-envelope fs-2 mb-2"></i><br />

                            </div>
                            <h4 className='fs-4 text-center '>Email</h4>
                            <NavLink className='text-decoration-none text-dark ' to="mailto:info@soft.co.in"> <h6 className='text-center fs-5'>info@cloudsoft.co.in</h6></NavLink>


                        </div>
                    </div>

                </div>

            </div>
        </div>
    </>
)
}

export default Contact